.modal{
    background-color: rgb(209, 209, 209);
    
}
.modal-item{
    padding: 5px 2px;
    margin: 0;
    transition: 0.3s linear;
}
.modal-item:hover{
   background-color: rgba(63, 63, 63, 0.336);
   /* color: wheat */

}
h2 {
    padding: 2px;
    margin: 2px;
    font-size: 17px;
}
span {
    padding: 3px;
    font-style: italic;
}
.modal-btn {
    margin: 5px;
    margin-top: 8px;
    background-color: tomato;
    padding: 4px 8px;
    border-radius: 3px;
}