.main {
  display: flex;
  justify-content: space-between;
  /* border: 1px solid red; */
}
.nav {
  /* border: 1px solid red; */
  background-color: #d9dde4;
  width: 100%;
  max-width: 320px;
  display: flex;  
  align-items: center;
  height: 30px;
}
.nav-item {
  padding: 10px;
}
.list {
  background: rgb(223, 223, 223);
  padding: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
  /* border: 1px solid red; */
  width: 100%;
  max-width: 300px;
}
.list-item {
  padding: 5px;
}
.instrum {
  width: 100%;
  max-width: 500px;
  display: flex;
 justify-content: space-around;
 /* border: 1px solid rgb(160, 30, 30); */
}
.controls {
  /* background: rgb(240, 239, 173); */
  padding: 10px;
  width: 100%;
  max-width: 210px;
  display: flex;
 justify-content: space-around;

}
.controls-btn {
  width: 100%;
  max-width: 50px;
  height: 30px;

  margin-right: 10px;
  border: 1px solid rgb(128, 125, 125);
  
}
.controls-btn img:hover {
  background-color: #018c91;

}
.controls-btn img{
  width: 100%;
  height: 100%;
  display: block;
  transition: 0.2s linear;
}
.active-floorplane {
  color: #018c91;
  font-size: 23px;
  font-weight: bold;
}